
<template>
	<div class="app-contain" :style='{"minHeight":"100vh","padding":"0","margin":"20px auto 60px","borderRadius":"20px","background":"none","width":"1200px","position":"relative","height":"100%"}'>
		<div class="bread_view">
			<el-breadcrumb separator=">" class="breadcrumb">
				<el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="12">
					<el-form-item label="课程名称" prop="kechengmingcheng">
						<el-input class="list_inp" v-model="form.kechengmingcheng" placeholder="课程名称"
							 type="text" 							:readonly="!isAdd||disabledForm.kechengmingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="学期" prop="xueqi">
						<el-input class="list_inp" v-model="form.xueqi" placeholder="学期"
							 type="text" 							:readonly="!isAdd||disabledForm.xueqi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="过程性成绩" prop="guochengxingchengji">
						<el-input class="list_inp" v-model.number="form.guochengxingchengji" placeholder="过程性成绩"
							 type="number" 							:readonly="!isAdd||disabledForm.guochengxingchengji?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="过程性成绩占比" prop="guochengxingchengjizhanbi">
						<el-input class="list_inp" v-model.number="form.guochengxingchengjizhanbi" placeholder="过程性成绩占比"
							 type="number" 							:readonly="!isAdd||disabledForm.guochengxingchengjizhanbi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="期中成绩" prop="qizhongchengji">
						<el-input class="list_inp" v-model.number="form.qizhongchengji" placeholder="期中成绩"
							 type="number" 							:readonly="!isAdd||disabledForm.qizhongchengji?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="期中成绩占比" prop="qizhongchengjizhanbi">
						<el-input class="list_inp" v-model.number="form.qizhongchengjizhanbi" placeholder="期中成绩占比"
							 type="number" 							:readonly="!isAdd||disabledForm.qizhongchengjizhanbi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="期末成绩占比" prop="qimochengjizhanbi">
						<el-input class="list_inp" v-model.number="form.qimochengjizhanbi" placeholder="期末成绩占比"
							 type="number" 							:readonly="!isAdd||disabledForm.qimochengjizhanbi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="期末成绩" prop="qimochengji">
						<el-input class="list_inp" v-model.number="form.qimochengji" placeholder="期末成绩"
							 type="number" 							:readonly="!isAdd||disabledForm.qimochengji?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="成绩" prop="chengji">
						<el-input class="list_inp" v-model="chengji" placeholder="成绩" readonly></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="学号" prop="xuehao">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.xuehao?true:false"
							v-model="form.xuehao" 
							placeholder="请选择学号"
							style="width:100%;"
							@change="xuehaoChange">
							<el-option v-for="(item,index) in xuehaoLists" :label="item" :value="item">
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="家长账号" prop="jiazhangzhanghao">
						<el-input class="list_inp" v-model="form.jiazhangzhanghao" placeholder="家长账号"
							 type="text" 							:readonly="!isAdd||disabledForm.jiazhangzhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="班主任账号" prop="banzhurenzhanghao">
						<el-input class="list_inp" v-model="form.banzhurenzhanghao" placeholder="班主任账号"
							 type="text" 							:readonly="!isAdd||disabledForm.banzhurenzhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="教师工号" prop="jiaoshigonghao">
						<el-input class="list_inp" v-model="form.jiaoshigonghao" placeholder="教师工号"
							 type="text" 							:readonly="!isAdd||disabledForm.jiaoshigonghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="教师姓名" prop="jiaoshixingming">
						<el-input class="list_inp" v-model="form.jiaoshixingming" placeholder="教师姓名"
							 type="text" 							:readonly="!isAdd||disabledForm.jiaoshixingming?true:false" />
					</el-form-item>
				</el-col>

			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick">取消</el-button>
				<el-button class="formModel_confirm" @click="save"
					type="success"
					>
					保存
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//基础信息
	const tableName = 'xueshengchengji'
	const formName = '学生成绩'
	//基础信息
	const breadList = ref([{
		name: formName
	}])
	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form表单
	const form = ref({
		kechengmingcheng: '',
		xueqi: '',
		guochengxingchengji: 0,
		guochengxingchengjizhanbi: 0,
		qizhongchengji: 0,
		qizhongchengjizhanbi: 0,
		qimochengjizhanbi: 0,
		qimochengji: 0,
		chengji: 0,
		xuehao: '',
		jiazhangzhanghao: '',
		banzhurenzhanghao: '',
		jiaoshigonghao: '',
		jiaoshixingming: '',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		kechengmingcheng : false,
		xueqi : false,
		guochengxingchengji : false,
		guochengxingchengjizhanbi : false,
		qizhongchengji : false,
		qizhongchengjizhanbi : false,
		qimochengjizhanbi : false,
		qimochengji : false,
		chengji : false,
		xuehao : false,
		jiazhangzhanghao : false,
		banzhurenzhanghao : false,
		jiaoshigonghao : false,
		jiaoshixingming : false,
	})
	const isAdd = ref(false)
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		kechengmingcheng: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		xueqi: [
		],
		guochengxingchengji: [
			{required: true,message: '请输入',trigger: 'blur'}, 
			{ validator: validateNumber, trigger: 'blur' },
		],
		guochengxingchengjizhanbi: [
			{required: true,message: '请输入',trigger: 'blur'}, 
			{ validator: validateNumber, trigger: 'blur' },
		],
		qizhongchengji: [
			{required: true,message: '请输入',trigger: 'blur'}, 
			{ validator: validateNumber, trigger: 'blur' },
		],
		qizhongchengjizhanbi: [
			{required: true,message: '请输入',trigger: 'blur'}, 
			{ validator: validateNumber, trigger: 'blur' },
		],
		qimochengjizhanbi: [
			{required: true,message: '请输入',trigger: 'blur'}, 
			{ validator: validateNumber, trigger: 'blur' },
		],
		qimochengji: [
			{required: true,message: '请输入',trigger: 'blur'}, 
			{ validator: validateNumber, trigger: 'blur' },
		],
		chengji: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		xuehao: [
		],
		jiazhangzhanghao: [
		],
		banzhurenzhanghao: [
		],
		jiaoshigonghao: [
		],
		jiaoshixingming: [
		],
	})
	//学号列表
	const xuehaoLists = ref([])
	const chengji =computed(()=>{
		let c = form.value
		let a = Number(c.guochengxingchengji)*Number(c.guochengxingchengjizhanbi)+Number(c.qizhongchengji)*Number(c.qizhongchengjizhanbi)+Number(c.qimochengji)*Number(c.qimochengjizhanbi)
		form.value.chengji = a?a.toFixed(2):0
		return a?a.toFixed(2):0
	})
	//methods

	//methods
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='kechengmingcheng'){
					form.value.kechengmingcheng = row[x];
					disabledForm.value.kechengmingcheng = true;
					continue;
				}
				if(x=='xueqi'){
					form.value.xueqi = row[x];
					disabledForm.value.xueqi = true;
					continue;
				}
				if(x=='guochengxingchengji'){
					form.value.guochengxingchengji = row[x];
					disabledForm.value.guochengxingchengji = true;
					continue;
				}
				if(x=='guochengxingchengjizhanbi'){
					form.value.guochengxingchengjizhanbi = row[x];
					disabledForm.value.guochengxingchengjizhanbi = true;
					continue;
				}
				if(x=='qizhongchengji'){
					form.value.qizhongchengji = row[x];
					disabledForm.value.qizhongchengji = true;
					continue;
				}
				if(x=='qizhongchengjizhanbi'){
					form.value.qizhongchengjizhanbi = row[x];
					disabledForm.value.qizhongchengjizhanbi = true;
					continue;
				}
				if(x=='qimochengjizhanbi'){
					form.value.qimochengjizhanbi = row[x];
					disabledForm.value.qimochengjizhanbi = true;
					continue;
				}
				if(x=='qimochengji'){
					form.value.qimochengji = row[x];
					disabledForm.value.qimochengji = true;
					continue;
				}
				if(x=='chengji'){
					form.value.chengji = row[x];
					disabledForm.value.chengji = true;
					continue;
				}
				if(x=='xuehao'){
					form.value.xuehao = row[x];
					disabledForm.value.xuehao = true;
					continue;
				}
				if(x=='jiazhangzhanghao'){
					form.value.jiazhangzhanghao = row[x];
					disabledForm.value.jiazhangzhanghao = true;
					continue;
				}
				if(x=='banzhurenzhanghao'){
					form.value.banzhurenzhanghao = row[x];
					disabledForm.value.banzhurenzhanghao = true;
					continue;
				}
				if(x=='jiaoshigonghao'){
					form.value.jiaoshigonghao = row[x];
					disabledForm.value.jiaoshigonghao = true;
					continue;
				}
				if(x=='jiaoshixingming'){
					form.value.jiaoshixingming = row[x];
					disabledForm.value.jiaoshixingming = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('jiaoshigonghao') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.jiaoshigonghao = json.jiaoshigonghao
				disabledForm.value.jiaoshigonghao = true;
			}
			if(json.hasOwnProperty('jiaoshixingming') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.jiaoshixingming = json.jiaoshixingming
				disabledForm.value.jiaoshixingming = true;
			}
		})
		context?.$http({
			url: `option/xuesheng/xuehao`,
			method: 'get'
		}).then(res=>{
			xuehaoLists.value = res.data.data
		})
		//由上级字段带出不可改
		disabledForm.value.jiazhangzhanghao = true;
		//由上级字段带出不可改
		disabledForm.value.banzhurenzhanghao = true;
	}
	//初始化
	//取消
	const backClick = () => {
		history.back()
	}
	//学号回调
	const xuehaoChange=()=>{
		context?.$http({
			url: `follow/xuesheng/xuehao?columnValue=` + form.value.xuehao,
			method: 'get'
		}).then(res=>{
			//带出家长账号字段
			if(res.data.data.jiazhangzhanghao){
				form.value.jiazhangzhanghao = res.data.data.jiazhangzhanghao
			}
			//带出班主任账号字段
			if(res.data.data.banzhurenzhanghao){
				form.value.banzhurenzhanghao = res.data.data.banzhurenzhanghao
			}
		})
	}
	//提交
	const save=()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									history.back()
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							history.back()
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
	
</script>
<style lang="scss" scoped>
	// 面包屑盒子
	.bread_view {
		border-radius: 0px;
		padding: 0px 20px 10px;
		margin: 0px auto;
		background: none;
		width: calc(100% - 0px);
		border-color: #66666650;
		border-width: 0 0 1px;
		position: relative;
		border-style: solid;
		:deep(.breadcrumb) {
			font-size: 14px;
			line-height: 1;
			.el-breadcrumb__separator {
				margin: 0 9px;
				color: #333;
				font-weight: 500;
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
					color: #333;
					display: inline-block;
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
					color: #333;
					display: inline-block;
				}
			}
		}
	}
	// 表单
	.add_form{
		border: 0px solid #27bacc30;
		border-radius: 0px;
		padding: 30px 7%;
		background: none;
		// form item
		:deep(.el-form-item) {
			border: 0px solid #eee;
			padding: 6px 0;
			margin: 0 0 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 background: none;
			 display: block;
			 width: auto;
			 min-width: 150px;
			 text-align: right;
			}
			// 内容盒子
			.el-form-item__content {
				display: flex;
